import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";

export interface ShapeCountsState {
  [id: string]: number;
}
export const shapeCountsSlice = createSlice({
  name: "shapeCounts",
  initialState: {} as ShapeCountsState,
  reducers: {
    increment: (state, { payload: id }: PayloadAction<string>) => {
      if (state[id] == null) {
        state[id] = 0;
      }
      state[id] += 1;
    },
  },
  extraReducers: {
    "overlays/add": (state, { payload: { layerType } }) => {
      if (typeof state[layerType] === "undefined") state[layerType] = 0;
      state[layerType] += 1;
    },
  },
});

export const selectShapeCounts = (state: RootState) => state.shapeCounts;
export const selectShapeCount = (shapeType: string) => (state: RootState) =>
  state.shapeCounts[shapeType];
export const { increment } = shapeCountsSlice.actions;
export default shapeCountsSlice.reducer;
