import { createAction, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";

export interface OverlaysState {
  [id: string]: any;
}

export const generateId = createAction("generateId");

let shapeCounts: any = {};
/**
 * returns simple unique ID for display
 * and also so it doesn't overwrite any geoJson in the OverlaysState object
 */
function getId(shapeType: string = "shape"): string {
  if (typeof shapeCounts?.[shapeType] === "undefined") {
    shapeCounts[shapeType] = 0;
  }
  shapeCounts[shapeType] += 1;
  return `${shapeType} ${shapeCounts?.[shapeType]}`;
}

export const overlaysSlice = createSlice({
  name: "overlays",
  initialState: {} as OverlaysState,
  reducers: {
    /*
    add: {
      reducer: (state, action: PayloadAction<{ id: string; overlay: any }>) => {
        let { id, overlay } = action.payload;
        state[id] = overlay;
      },
      prepare: ({ id, layerType, overlay }: any) => {
        if (typeof id === "undefined") id = getId(layerType);
        //const shapeType = overlay?.geometry?.type?.toLowerCase?.() ?? "shape";
        //const id = getId(shapeType);
        return { payload: { id, layerType, overlay } };
      },
    },
    */
    add: (state, action: PayloadAction<{ id: string; overlay: any }>) => {
      let { id, overlay } = action.payload;
      state[id] = overlay;
    },
    delete: (state, action: PayloadAction<string>) => {
      delete state[action.payload];
    },
  },
});

export const selectOverlays = (state: RootState) => state.overlays;
export const { add, delete: del } = overlaysSlice.actions;
export default overlaysSlice.reducer;
