import React from "react";
import styled from "styled-components";
import * as d3 from "d3";
import * as topojson from "topojson-client";
import world from './world-110m.json'
import places from './world-places.json'

const Div = styled.div`
  .land {
    fill: darkolivegreen;
    stroke-opacity: 1;
  }
  .arcs {
    opacity: .1;
    stroke: gray;
    stroke-width: 3;
  }
  .flyers {
    stroke-width: 1;
    opacity: .6;
    stroke: darkred;
  }
  .arc, .flyer {
    stroke-linejoin: round;
    fill: none;
  }
  .arc {}
  .flyer {}
  .flyer:hove {}
`;

d3.select(window).on("mousemove", mousemove).on("mouseup", mouseup);

const width = 960,
  height = 500;

const proj = d3.geoOrthographic() //d3.geo.orthographic()
  .translate([width / 2, height / 2])
  .clipAngle(90)
  .scale(220);

const sky = d3.geoOrthographic() //d3.geo.orthographic()
  .translate([width / 2, height / 2])
  .clipAngle(90)
  .scale(300);

const path = d3.geoPath() //d3.geo.path()
  .projection(proj).pointRadius(2);

const swoosh = d3 //d3.svg
  .line()
  .x(function (d) {
    return d[0];
  })
  .y(function (d) {
    return d[1];
  })
  //.interpolate("cardinal")
  //.tension(0.0);

const links = [],
  arcLines = [];

const svg = d3.create('svg')
  .attr('width', width)
  .attr('height', height)
  .on('mousedown', mousedown)

/*
const svg = d3
  .select("body")
  .append("svg")
  .attr("width", width)
  .attr("height", height)
  .on("mousedown", mousedown);

queue()
  .defer(d3.json, "world-110m.json")
  .defer(d3.json, "places.json")
  .await(ready);
  */

function ready(error, world, places) {
  const ocean_fill = svg
    .append("defs")
    .append("radialGradient")
    .attr("id", "ocean_fill")
    .attr("cx", "75%")
    .attr("cy", "25%");
  //ocean_fill.append("stop").attr("offset", "5%").attr("stop-color", "#fff");
  ocean_fill.append("stop").attr("offset", "5%").attr("stop-color", "blue");
  ocean_fill
    .append("stop")
    .attr("offset", "100%")
    //.attr("stop-color", "#ababab");
    .attr("stop-color", "darkblue");

  const globe_highlight = svg
    .append("defs")
    .append("radialGradient")
    .attr("id", "globe_highlight")
    .attr("cx", "75%")
    .attr("cy", "25%");
  globe_highlight
    .append("stop")
    .attr("offset", "5%")
    .attr("stop-color", "#ffd")
    .attr("stop-opacity", "0.6");
  globe_highlight
    .append("stop")
    .attr("offset", "100%")
    .attr("stop-color", "#ba9")
    .attr("stop-opacity", "0.2");

  const globe_shading = svg
    .append("defs")
    .append("radialGradient")
    .attr("id", "globe_shading")
    .attr("cx", "55%")
    .attr("cy", "45%");
  globe_shading
    .append("stop")
    .attr("offset", "30%")
    .attr("stop-color", "#fff")
    .attr("stop-opacity", "0");
  globe_shading
    .append("stop")
    .attr("offset", "100%")
    .attr("stop-color", "#505962")
    .attr("stop-opacity", "0.3");

  const drop_shadow = svg
    .append("defs")
    .append("radialGradient")
    .attr("id", "drop_shadow")
    .attr("cx", "50%")
    .attr("cy", "50%");
  drop_shadow
    .append("stop")
    .attr("offset", "20%")
    .attr("stop-color", "#000")
    .attr("stop-opacity", ".5");
  drop_shadow
    .append("stop")
    .attr("offset", "100%")
    .attr("stop-color", "#000")
    .attr("stop-opacity", "0");

  svg
    .append("ellipse")
    .attr("cx", 440)
    .attr("cy", 450)
    .attr("rx", proj.scale() * 0.9)
    .attr("ry", proj.scale() * 0.25)
    .attr("class", "noclicks")
    .style("fill", "url(#drop_shadow)");

  svg
    .append("circle")
    .attr("cx", width / 2)
    .attr("cy", height / 2)
    .attr("r", proj.scale())
    .attr("class", "noclicks")
    .style("fill", "url(#ocean_fill)");

  svg
    .append("path")
    //.datum(topojson.object(world, world.objects.land))
    .datum(topojson.feature(world, world.objects.land).geometry)
    .attr("class", "land noclicks")
    .attr("d", path);

  svg
    .append("circle")
    .attr("cx", width / 2)
    .attr("cy", height / 2)
    .attr("r", proj.scale())
    .attr("class", "noclicks")
    .style("fill", "url(#globe_highlight)");

  svg
    .append("circle")
    .attr("cx", width / 2)
    .attr("cy", height / 2)
    .attr("r", proj.scale())
    .attr("class", "noclicks")
    .style("fill", "url(#globe_shading)");

  svg
    .append("g")
    .attr("class", "points")
    .selectAll("text")
    .data(places.features)
    .enter()
    .append("path")
    .attr("class", "point")
    .attr("d", path);

  // spawn links between cities as source/target coord pairs
  places.features.forEach(function (a) {
    places.features.forEach(function (b) {
      if (a !== b) {
        links.push({
          source: a.geometry.coordinates,
          target: b.geometry.coordinates,
        });
      }
    });
  });

  // build geoJSON features from links array
  links.forEach(function (e, i, a) {
    const feature = {
      type: "Feature",
      geometry: { type: "LineString", coordinates: [e.source, e.target] },
    };
    arcLines.push(feature);
  });

  svg
    .append("g")
    .attr("class", "arcs")
    .selectAll("path")
    .data(arcLines)
    .enter()
    .append("path")
    .attr("class", "arc")
    .attr("d", path);

  svg
    .append("g")
    .attr("class", "flyers")
    .selectAll("path")
    .data(links)
    .enter()
    .append("path")
    .attr("class", "flyer")
    .attr("d", function (d) {
      return swoosh(flying_arc(d));
    });

  refresh();
}

function flying_arc(pts) {
  const source = pts.source,
    target = pts.target;

  const mid = location_along_arc(source, target, 0.5);
  const result = [proj(source), sky(mid), proj(target)];
  return result;
}

function refresh() {
  svg.selectAll(".land").attr("d", path);
  svg.selectAll(".point").attr("d", path);

  svg
    .selectAll(".arc")
    .attr("d", path)
    .attr("opacity", function (d) {
      return fade_at_edge(d);
    });

  svg
    .selectAll(".flyer")
    .attr("d", function (d) {
      return swoosh(flying_arc(d));
    })
    .attr("opacity", function (d) {
      return fade_at_edge(d);
    });
}

function fade_at_edge(d) {
  const centerPos = proj.invert([width / 2, height / 2]),
    //arc = d3.geo.greatArc(),
    arc = { distance: () => 100}, // = d3.geoGreatArc(), // doesn't exist in d3 v7 (example uses v3)
    start,
    end;
  // function is called on 2 different data structures..
  if (d.source) {
    (start = d.source), (end = d.target);
  } else {
    start = d.geometry.coordinates[0];
    end = d.geometry.coordinates[1];
  }

  const start_dist = 1.57 - arc.distance({ source: start, target: centerPos }),
    end_dist = 1.57 - arc.distance({ source: end, target: centerPos });

  const fade = d3.scaleLinear().domain([-0.1, 0]).range([0, 0.1]);
  const dist = start_dist < end_dist ? start_dist : end_dist;

  //return fade(dist);
  return 1
}

function location_along_arc(start, end, loc) {
  const interpolator = d3.geoInterpolate(start, end);
  return interpolator(loc);
}

// modified from http://bl.ocks.org/1392560
const m0, o0;
function mousedown(event) {
  //m0 = [d3.event.pageX, d3.event.pageY];
  m0 = [event.pageX, event.pageY];
  o0 = proj.rotate();
  //d3.event.preventDefault();
  event.preventDefault();
}
function mousemove(event) {
  if (m0) {
    //const m1 = [d3.event.pageX, d3.event.pageY],
    const m1 = [event.pageX, event.pageY],
      o1 = [o0[0] + (m1[0] - m0[0]) / 6, o0[1] + (m0[1] - m1[1]) / 6];
    o1[1] = o1[1] > 30 ? 30 : o1[1] < -30 ? -30 : o1[1];
    proj.rotate(o1);
    sky.rotate(o1);
    refresh();
  }
}
function mouseup(event) {
  if (m0) {
    //mousemove();
    mousemove(event);
    m0 = null;
  }
}

export const D3Globe = () => {
  const ref = React.useRef()
  React.useEffect(() => {
    ready(null, world, places);
    ref.current.appendChild(svg.node());
  }, [])
  return (
    <Div className="my-map-container" ref={ref}></Div>
  )
}

export default D3Globe
